@import '../../appScss/colors.scss';
@import '../../appScss/mixins.scss';

.TeamMember {
  display: flex;
  padding: 10px;
  width: 500px;
  cursor: pointer;
  border-radius: 5px;
  transition: linear 0.3s;
  align-items: center;
  justify-content: space-between;
  border-color: transparent;
  border: 1px solid var(--secondary-color);

  &:hover {
    -webkit-box-shadow: 0px 3px 20px 0px var(--teamMember-shadow-color);
    -moz-box-shadow: 0px 3px 20px 0px var(--teamMember-shadow-color);
    box-shadow: 0px 3px 20px 0px var(--teamMember-shadow-color);

    .TeamMember__avatar {
      border-radius: 20px;
    }
  }

  @include onTablet {
    width: 300px;
    padding: 5px;
  }

  @include onPhone {
    gap: 10px;
    box-sizing: border-box;
    max-width: 600px;
    padding: 10px;
    width: 100%;
  }

  &__info {
    display: flex;
    gap: 10px;
  }
  &__avatar {
    display: block;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    -webkit-box-shadow: 0px 0px 14px 0px rgba(184, 184, 184, 1);
    -moz-box-shadow: 0px 0px 14px 0px rgba(184, 184, 184, 1);
    box-shadow: 0px 0px 14px 0px rgba(184, 184, 184, 1);
    transition: linear 0.3s;

    @include onTablet {
      width: 80px;
      height: 80px;
    }

    @include onPhone {
      width: 70px;
      height: 70px;
    }
  }

  &__description {
    margin-top: 10px;
    align-self: flex-start;
  }

  &__name {
    font-size: 20px;

    @include onTablet {
      font-size: 13px;
    }

    @include onPhone {
      font-size: 10px;
    }
  }

  &__speciality {
    color: var(--secondary-color);
    margin-top: 10px;

    @include onTablet {
      font-size: 10px;
    }
  }
}

.activeMember {
  transform: scale(1.1);

  .TeamMember__avatar {
    border-radius: 20px;
  }
}

[theme='dark'] {
  .TeamMember {
    border: 1px solid var(--elements-color);

    &__avatar {
      -webkit-box-shadow: 0px 0px 14px 0px #6d6d6d9f;
      -moz-box-shadow: 0px 0px 14px 0px #6d6d6d9f;
      box-shadow: 0px 0px 14px 0px #6d6d6d9f;
    }
  }
}
