@import './colors.scss';
@import './typography.scss';
@import './mixins';

body {
  font-family: Mont, sans-serif;
}

#root {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  min-height: 100vh;

  @include onTablet {
    overflow: hidden;
  }
}

header,
footer {
  flex-shrink: 0;
}

main {
  flex-grow: 1;
  margin-inline: auto;

  @include onDesktop {
    width: 1136px;
  }

  @include onTablet {
    width: calc(100vw - 48px);
  }

	max-width: 1136px;
  // @include onTablet {
  //   width: 1136px;
  //   margin: 0 auto;
  // }
}

button {
  cursor: pointer;
}
