@import '../../appScss/index.scss';

.favourite_icon {
  position: relative;
	color: inherit;
  cursor: pointer;
}

.circle {
  position: absolute;
  top: -8px;
  left: 8px;
  background-color: #eb5757;
  color: white;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  border: 2px solid white;
}

.counter {
  display: block;
  width: 100%;
  line-height: 13px;
  font-size: 9px;
  text-align: center;
}

.icon_color {
	color: var(--primary-color);
}

[theme='dark'] {
  .dark_theme_icon_color {
    filter: invert(100%);
  }
}
